<template>
  <div class="earnings">
    <div class="mid">
      <div class="list mescroll" id="list">
        <div class="list-data" id="listData">
          <div class="item" v-for="(item,index) in list" :key="index">
            <div class="left">
              <p class="title">{{item.activityName}}</p>
              <p class="text">{{item.acDes}}</p>
              <p class="time">{{item.createDate}}</p>
            </div>
            <div class="right">
              <p class="price">{{item.tmoney.toFixed(2)}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getEarningsList, getMyEarningsList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {},
  data() {
    return {
      list: [],
      mescroll: null
    };
  },
  methods: {
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        acode: this.$route.query.acode
      };

      if (this.$route.query.type == "1") {
        getMyEarningsList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data);
            } else {
              this.$toast.fail(res.msg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      } else {
        getEarningsList(dataObj)
          .then(res => {
            if (res.code == 0) {
              successCallback && successCallback(res.data);
            } else {
              this.$toast.fail(res.msg);
            }
          })
          .catch(res => {
            errorCallback && errorCallback();
            console.log(res);
          });
      }
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "我的收益";
    setTimeout(() => {
      this.startMescroll();
    }, 500);
  }
};
</script>
<style lang="scss" scoped>
.earnings {
  .mid {
    height: calc(100vh);
    .list {
      .item {
        padding: 10px 15px;
        display: flex;
        align-items: center;
        border-bottom: 1pt solid #e1e1e1;
        .left {
          flex: 2;
          line-height: 25px;
          .title {
            font-size: 15px;
          }
          .text {
            color: #666;
            font-size: 15px;
          }
          .time {
            color: #999;
            font-size: 13px;
          }
        }
        .right {
          text-align: right;
          .price {
            font-size: 18px;
            color: $base-color;
          }
        }
      }
    }
    .bottom {
      background: #eb5252;
      position: fixed;
      bottom: 0;
      left: 0;
      color: #fff;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
    }
  }
}
</style>
